.header {
  text-align: center;
  color: #f8f8f8;
  position: relative;
  background-color: #021427;
  padding-top: 5%;
  padding-bottom: 5%;
}

small {
  text-align: center;
  width: 100%;
  display: block;
}

.header > h1 {
  margin: 0;
  text-shadow: 2px 2px 3px rgba(0,0,0,0.6);
  font-size: 4em;
}

.container {
  text-align: center;
  padding-top: 2%;
  width: 100%;
  height: 65vh;
}

@media (max-width: 623px) {
  .header > h1 {
    margin: 0;
    margin-top: 5px;
    text-shadow: 2px 2px 3px rgba(0,0,0,0.6);
    font-size: 2em;
  }

  .header > h3 {
    font-size: 1em;
  }
}

.search-container{
  width: 490px;
  display: block;
  margin: 0 auto;
}

.search-input{
  margin: 0 auto;
  width: 100%;
  height: 45px;
  padding: 0 20px;
  font-size: 1rem;
  border: 1px solid #D0CFCE;
  outline: none;
 }

.search-icon{
  position: relative;
  float: right;
  width: 75px;
  height: 75px;
  top: -62px;
  right: -45px;
}
